
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

* {
  margin: 0;
  padding: 0;
}
html, body {
  width: 100%;
  height: 100%;
}
ul, li {
  list-style: none;
}
.citySwtich {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  z-index: 9999;
  background: #fff;
}
.citySwtich .cityHeader {
    height: 1.17188rem;
    min-height: 1.17188rem;
    border-bottom: 0.01563rem solid #eee;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.citySwtich .cityHeader h2 {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      margin: 0 auto;
}
.citySwtich .cityHeader h2 em {
        margin: auto;
        color: #fe4f53;
        font-size: 0.46875rem;
        font-weight: normal;
}
.citySwtich .cityHeader h2 span {
        font-size: 0.25rem;
        color: #000;
        margin: auto 0.25rem;
}
.citySwtich .cityMain {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 0 0.5625rem;
    position: relative;
    background: #fff;
}
.citySwtich .cityMain .list {
      margin: 0 auto;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      overflow-y: scroll;
}
.citySwtich .cityMain .list .h5 {
        height: 0.78125rem;
        line-height: 0.78125rem;
        font-size: 0.40625rem;
        color: #000;
}
.citySwtich .cityMain .list .liBox {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        height: auto;
        margin: 0 auto 0.625rem;
}
.citySwtich .cityMain .list .liBox li {
          margin: 0.1875rem 0.3125rem 0 0;
          width: 2.73438rem;
          height: 0.85938rem;
          line-height: 0.85938rem;
          text-align: center;
          color: #666;
          font-size: 0.375rem;
          border: 0.01563rem solid #eee;
          border-radius: 0.1875rem;
}
.citySwtich .cityMain .list .liBox li:nth-child(3n) {
          margin-right: 0;
}
.citySwtich .cityMain .list .hA {
        font-size: 0.40625rem;
        margin: 0.25rem 0;
        width: 0.78125rem;
        height: 0.78125rem;
        background: #666;
        border-radius: 50%;
        color: #fff;
        text-align: center;
        line-height: 0.78125rem;
}
.citySwtich .cityMain .list .hA.active {
        background: #fe4f53;
}
.citySwtich .cityMain .hot .hA {
      width: 2.5rem;
      border-radius: 0.1875rem;
}
.citySwtich .cityMain .hot .liBox li {
      background: #eee;
      border: 0;
}
.citySwtich .cityMain .alpss {
      position: fixed;
      top: 50%;
      left: 50%;
      margin-top: -0.625rem;
      margin-left: -0.625rem;
      width: 1.25rem;
      height: 1.25rem;
      background: rgba(0, 0, 0, 0.6);
      border-radius: 0.40625rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      z-index: 99999;
}
.citySwtich .cityMain .alpss span {
        font-size: 0.5rem;
        color: #fff;
        margin: auto;
}
.citySwtich .cityMain .alphabet {
      padding: 0.25rem 0;
      position: absolute;
      top: 10%;
      right: 0;
      width: 0.5625rem;
      height: 80%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
}
.citySwtich .cityMain .alphabet li {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        font-size: 0.375rem;
        color: #fe4f53;
        margin: auto;
}
.citySwtich .cityMain .alphabet li.active {
        color: #fe4f53;
        font-size: 0.46875rem;
}
